const ReflectPage111: React.FC<ReflectPageProps> = ({ 
  isDesktop = false,
  toggleMenu 
}) => {
  const [analysisType, setAnalysisType] = useState<'style' | 'impact' | 'result'>('style');
  const [message, setMessage] = useState('');
  const [attachments, setAttachments] = useState<File[]>([]);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [analysis, setAnalysis] = useState<any>(null);
  const [error, setError] = useState<string | null>(null);

  const handleAttachmentAdd = (files: FileList) => {
    const newFiles = Array.from(files);
    setAttachments([...attachments, ...newFiles]);
  };
  
  const handleAttachmentRemove = (index: number) => {
    setAttachments(attachments.filter((_, i) => i !== index));
  };
  
  // Update this function in your ReflectPage111.tsx file
  const handleAnalyze = async (type: 'style' | 'impact' | 'result') => {
    if (!message && attachments.length === 0) return;
    
    setAnalysisType(type);
    setIsAnalyzing(true);
    setError(null);
    setAnalysis(null); // Clear previous analysis
    
    try {
      // Log before calling the function
      console.log('Calling analyze-text function with:', { message, type });
      
      // Call the analyze-text function directly without authentication checks
      const functionResponse = await supabase.functions.invoke('analyze-text', {
        body: { message, type }
      });
      
      // Log the raw response for debugging
      console.log('Raw function response:', functionResponse);
      
      const { data: analysisData, error: functionError } = functionResponse;
      
      if (functionError) {
        throw new Error(`Analysis function error: ${functionError.message || JSON.stringify(functionError)}`);
      }
      
      if (!analysisData) {
        throw new Error('Analysis function returned empty data');
      }
      
      // Log the analysis data
      console.log('Analysis data received:', analysisData);
      
      // Check if analysis data has the expected structure
      if (typeof analysisData !== 'object') {
        throw new Error(`Invalid analysis data format. Expected object, got: ${typeof analysisData}`);
      }
      
      // First, save the message to the messages table
      const { data: messageData, error: messageError } = await supabase
        .from('messages')
        .insert({
          content: message,
          created_at: new Date().toISOString()
        })
        .select('id')
        .single();
        
      if (messageError) {
        throw new Error(`Failed to save message: ${messageError.message}`);
      }
      
      if (!messageData) {
        throw new Error('Message was saved but no ID was returned');
      }
      
      // Then, save the analysis result with a reference to the message
      const { error: analysisError } = await supabase
        .from('analysis_results')
        .insert({
          message_id: messageData.id,
          type,
          metrics: analysisData
        });
        
      if (analysisError) {
        throw new Error(`Failed to save analysis result: ${analysisError.message}`);
      }
      
      // Update state with analysis result
      setAnalysis(analysisData);
      
    } catch (error) {
      // Enhanced error logging
      console.error('Error analyzing message:', error);
      
      // Extract error details for display
      let errorMessage = 'Unknown error occurred';
      let errorDetails = '';
      
      if (error instanceof Error) {
        errorMessage = error.message;
        errorDetails = error.stack || '';
      } else if (typeof error === 'object' && error !== null) {
        try {
          errorMessage = JSON.stringify(error);
        } catch (e) {
          errorMessage = 'Unserializable error object';
        }
      }
      
      // Set a detailed error message for display
      setError(`Analysis failed: ${errorMessage}`);
      
      // Create a debugging display in place of the analysis
      setAnalysis({
        debug: true,
        error: errorMessage,
        errorDetails: errorDetails,
        timestamp: new Date().toISOString(),
        requestData: {
          type,
          messageLength: message.length,
          hasAttachments: attachments.length > 0
        }
      });
      
      return;
    } finally {
      setIsAnalyzing(false);
    }
  };

  const isButtonDisabled = isAnalyzing || (!message && attachments.length === 0);

  return (
    <PageContainer isDesktop={isDesktop}>
      {/* Only show Navigation in mobile mode */}
      {!isDesktop && <Navigation toggleMobileMenu={toggleMenu} />}
      
      <ContentArea isDesktop={isDesktop}>
        {/* Show desktop header only in desktop mode */}
        {isDesktop && <DesktopHeader>Reflect: Analyze Your Communication</DesktopHeader>}
        
        {error && (
          <div style={{ 
            backgroundColor: '#ffdddd', 
            color: '#ff0000', 
            padding: '10px', 
            margin: '10px', 
            borderRadius: '4px',
            textAlign: 'center'
          }}>
            {error}
          </div>
        )}
        
        <ReflectionOutput 
          analysisType={analysisType}
          analysis={analysis}
          isLoading={isAnalyzing}
        />
        
        <MessageInput 
          value={message}
          onChange={setMessage}
          onAttach={handleAttachmentAdd}
          attachments={attachments}
          onAttachmentRemove={handleAttachmentRemove}
        />
        
        {/* Analyze buttons */}
        <ButtonsContainer isDesktop={isDesktop}>
          <AnalysisButton 
            active={analysisType === 'style'} 
            onClick={() => handleAnalyze('style')}
            disabled={isButtonDisabled}
          >
            Style
          </AnalysisButton>
          <AnalysisButton 
            active={analysisType === 'impact'} 
            onClick={() => handleAnalyze('impact')}
            disabled={isButtonDisabled}
          >
            Impact
          </AnalysisButton>
          <AnalysisButton 
            active={analysisType === 'result'} 
            onClick={() => handleAnalyze('result')}
            disabled={isButtonDisabled}
          >
            Result
          </AnalysisButton>
        </ButtonsContainer>
      </ContentArea>
      
      {/* Logo at the bottom - only shown in mobile mode */}
      {!isDesktop && (
        <LogoContainer isDesktop={isDesktop}>HIT</LogoContainer>
      )}
    </PageContainer>
  );
};

export default ReflectPage111;